.dialog {
  width: calc(100% - 220px);
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
  opacity: 0;
  -webkit-transition: opacity .5s ease;
  -o-transition: opacity .5s ease;
  transition: opacity .5s ease;
}
.dialog-show {
  opacity: 1;
}
.dialog-inner {
  position: absolute;
  right:calc(50% - 180px);
  width: 360px;
  min-height: 180px;
  margin-top: 150px;
  padding: 15px;
  background: #ffffff;
  -webkit-box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2),
    0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
          box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2),
    0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-transition: transfrom .5s ease;
  -o-transition: transfrom .5s ease;
  transition: transfrom .5s ease;
}
.dialog-inner-show{
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
header.dialog-header {
  padding: 19px 20px;
  font-size: 26px;
  color: #444;
  font-weight: bold;
}
section.dialog-tips {
  padding: 25px 20px;
  font-size: 18px;
  color: #666;
}



